.Burger {
    width: 100%;
    margin: auto;
    height: 320px;
    overflow: scroll;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
}

@media (min-width: 500px) and (min-height: 400px) {
    .Burger {
        width: 400px;
        height: 350px;
    }
}

@media (min-width: 500px) and (min-height: 401px) {
    .Burger {
        width: 400px;
        height: 350px;
    }
}

@media (min-width: 1000px) and (min-height: 700px) {
    .Burger {
        width: 500px;
        height: 350px;
    }
}


